var _self;
export default {
	data() {
		return {
			dataList:[],
			keywords:""
		};
	},
	onLoad() {
		_self = this;
	},
	methods: {
		onSearch(e){
			this.keywords  = e 
			this.downCallback()
		},
		onTerm(e){
			console.log(e.value.project_id);
			_self.$Router.push({ name: 'alarm-details', params: {id:e.value.project_id,name:e.value.wrong_name}})
		},
		/*下拉刷新的回调 */
		downCallback(mescroll) {
			//联网加载数据
			this.getListDataFromNet(
				0,
				1,
				data => {
					console.log(data);
					//联网成功的回调,隐藏下拉刷新的状态
					mescroll.endSuccess();
					//设置列表数据
					// this.dataList=[];
					this.dataList = data;
				},
				() => {
					//联网失败的回调,隐藏下拉刷新的状态
					mescroll.endErr();
				}
			);
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		upCallback(mescroll) {
			//联网加载数据
			this.getListDataFromNet(mescroll.num, mescroll.size, (curPageData)=>{
				//联网成功的回调,隐藏下拉刷新和上拉加载的状态;
				// console.log("i="+this.i+", mescroll.num=" + mescroll.num + ", mescroll.size=" + mescroll.size + ", curPageData.length=" + curPageData.length);
				mescroll.endSuccess(curPageData.length);
				//设置列表数据
				if(mescroll.num == 1) this.dataList = []; //如果是第一页需手动制空列表
				this.dataList=this.dataList.concat(curPageData); //追加新数据
			}, () => {
				//联网失败的回调,隐藏下拉刷新的状态
				mescroll.endErr();
			})
		},
		/*联网加载列表数据
			在您的实际项目中,请参考官方写法: http://www.mescroll.com/uni.html#tagUpCallback
			请忽略getListDataFromNet的逻辑,这里仅仅是在本地模拟分页数据,本地演示用
			实际项目以您服务器接口返回的数据为准,无需本地处理分页.
			* */
		async getListDataFromNet(pageNum, pageSize, successCallback, errorCallback) {
			try {
				const res = await this.$req.ajax({
					path: this.$api.alarm,
					data: {
						goods_type: this.i + 1,
						page: pageNum,
						pagesize: pageSize,
						keywords:this.keywords
					}
				});
				let { data, code } = res.data;
				console.log(code);
				if (code == 2000) {
					
					successCallback && successCallback(data);
				} else {
					
					uni.showToast({
						title: '请登录后再进行操作!',
						icon: "none"
					});
					setTimeout({function(){
						uni.redirectTo({
							url:"/pages/user/login"
						})
					}},1000)
				}
			} catch (e) {
				console.log(e);
			}
		}
	}
};
